import { useForm } from "react-hook-form";
import { atom, useRecoilState, useRecoilValue, useSetRecoilState } from "recoil";
import { ITodo, categoriesState, categoryState, todoSelector, todoState } from "../common/atoms";
import CreateTodo from "./CreateTodo";
import Todo from "./Todo";
import { useEffect } from "react";
import { styled } from "styled-components";

const Container = styled.div`
    height: 100vh;
    position: relative;
    background-image: url('https://images.unsplash.com/photo-1542480118439-cbba7870f6f7?q=80&w=776&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
    background-size: cover;
`;

const CategoryBar = styled.div`
    padding: 8px 10px;
    select {
        background-color: rgba(0, 0, 0, 0.5);
        color: rgba(255, 255, 255, 0.5);
        border-color: hsla(0, 0%, 100%, 0.5);
    }
    button {
        background-color: transparent;
        color: rgba(255, 255, 255, 0.5);
        border: none;
        font-size: 18px;
        margin-left: 2px;
    }
`;


const TodosScreen = () => {
    const categories = useRecoilValue(categoriesState);
    const addCategory = useSetRecoilState<string[]>(categoriesState);
    const setCategoryState = useSetRecoilState<string>(categoryState);
    const todo = useRecoilValue(todoSelector);
    const [todos, setTodos] = useRecoilState(todoState);

    useEffect(() => {
        console.log("set LocalStorage");
        localStorage.setItem("todos", JSON.stringify(todos));
    }, [todos]);

    useEffect(() => {
        localStorage.setItem("categories", JSON.stringify(categories));
    }, [categories])

    const onInput = (event: React.FormEvent<HTMLSelectElement>) => {
        console.log(event.currentTarget.value);
        setCategoryState(event.currentTarget.value);
    }

    const onAddCategory = () => {
        const category = prompt(' ');
        console.log(category);
        if (category != null) {
            addCategory(current => {
                let newCategories = [...current, category];
                return newCategories;
            });
        }
    }

    return <Container>
        <CategoryBar>
            <select onInput={onInput}>
                {categories.map(v => <option value={v}>{v}</option>)}
            </select>
            <button onClick={onAddCategory}>+</button>
        </CategoryBar>
        
        <ul>
            {todo.map(v => <Todo {...v} />)}
        </ul>
        <CreateTodo />
    </Container>;
}

export default TodosScreen;

