import React from "react";
import { ITodo, categoriesState, todoState } from "../common/atoms";
import { useRecoilValue, useSetRecoilState } from "recoil";
import { styled } from "styled-components";

const Item = styled.div`
    display: flex;
    flex-direction: row;
    color: rgba(255, 255, 255, 0.5);
    /* background-color: rgba(0, 0, 0, 0.12); */

    span {
        padding: 2px 10px;
    }

    button {
        font-size: 10px;
        border: none;
        background-color: rgba(255, 255, 255, 0.1);
        color: rgba(0, 0, 0, 0.6);
        margin: 2px 1px;
        border-radius: 4px;
    }
`;

const Todo = ({ text, id, category }: ITodo) => {
    const todos = useRecoilValue(todoState);
    const categories = useRecoilValue(categoriesState);
    const setTodos = useSetRecoilState(todoState)
    const onClick = (event: React.MouseEvent<HTMLButtonElement>) => {
        const {
            currentTarget: { name }
        } = event;
        setTodos(current => {
            // index 
            const targetIndex = current.findIndex(v => v.id === id);
            //  todo
            const newTodo = { text, id, category: name as any };
            // index   
            const start  = current.slice(0, targetIndex);
            // index   
            const end  = current.slice(targetIndex + 1, current.length - targetIndex + 1);
            //  + todo +   
            const newTodos  = [...start, newTodo, ...end];
            return newTodos;
        });
    }

    const onDeleteClick = (event: React.FormEvent<HTMLButtonElement>) => {
        const {
            currentTarget: { name }
        } = event;
        setTodos(current => {
            // index 
            const targetIndex = current.findIndex(v => v.id === id);
            // index   
            const start  = current.slice(0, targetIndex);
            // index   
            const end  = current.slice(targetIndex + 1, current.length - targetIndex + 1);
            //  +   
            const newTodos  = [...start, ...end];
            return newTodos;
        });

    }

    console.log(`id=${id}, text=${text}, category=${category}`);
    return <Item key={id}>
        <span>{text}</span>
        {categories.map(v => <button name={v} onClick={onClick}>{v}</button>)}
        <button onClick={onDeleteClick}>Delete</button>
    </Item>;
}

export default Todo;